html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

.descripcion {
  word-wrap: break-word;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  max-width: 110rem;
  margin: auto;
  width: 90%;
}

.espacio {
  width: 100%;
  margin-bottom: 7rem;
}

.espacio_p {
  padding-bottom: 4rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 15rem;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 600;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.alerta {
  padding: 0.5rem 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 1rem;
}
.alerta.error {
  color: #BA0534;
}
.alerta.exito {
  color: #9f425a;
}

.no {
  display: none !important;
}

.inicio {
  display: block;
}

.no2 {
  display: none !important;
}

.imghabitaciones {
  display: block;
}

.cuadro {
  position: absolute;
  top: 13.5rem;
  width: 100%;
  height: 50rem;
  background-color: rgba(0, 0, 0, 0.507);
  z-index: 2;
}

.cuadro {
  height: 50.5rem;
}

.video33 {
  display: flex;
  width: 100%;
  height: 50rem;
  align-items: end;
  display: flex;
  justify-content: left;
}
.video33 .header-flex {
  padding: 3rem;
  z-index: 4;
  align-items: center;
  display: flex;
  justify-content: center;
  border-radius: 50%;
}
.video33 .header-flex .part2_header-flex p:nth-child(1) {
  font-size: 4rem;
  color: #ffffff;
}
.video33 .header-flex .part2_header-flex p:nth-child(2) {
  font-size: 1.4rem;
  color: #ffffff;
}
.video33 .header-flex .part2_header-flex p:nth-child(3) {
  font-size: 1.4rem;
  color: #CF5A60;
  font-style: italic;
}

video {
  width: 100%;
}

.video {
  position: absolute;
  top: 9rem;
  height: 55rem;
  width: 100%;
  overflow: hidden;
  z-index: 1;
}

.redes {
  display: flex;
  gap: 2rem;
}
.redes .redes_div {
  background-color: #ffffff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
}
.redes .redes_div a i {
  color: #ac1c43;
}

.header {
  background-color: #222;
  height: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .menu #btn_menu {
  display: none;
}
.header .header_cont {
  align-items: center;
  display: flex;
  justify-content: space-between;
  gap: 4rem;
}
.header .header_cont .navegacion {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header .header_cont .navegacion a {
  display: inline-block;
  color: #ffffff;
  font-size: 1.3rem;
  padding: 1rem;
}
.header .header_cont .navegacion a:hover {
  padding: 1rem;
  background-color: #f2f0f0;
  color: #CF5A60;
  border-radius: 1rem;
  -webkit-transition: ease-out 0.3s;
  -moz-transition: ease-out 0.3s;
  transition: ease-out 0.3s;
}

.imagen_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .img_header {
  width: 100%;
  height: 60rem;
}
.imagen_header .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 60rem;
  background-color: hsla(0, 0%, 8%, 0.524);
}
.imagen_header .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.slideshow {
  width: 100%;
  height: 60rem;
  position: relative;
  overflow: hidden;
}
.slideshow ul, .slideshow ol {
  list-style: none;
  margin: 0;
}

.slideshow2 {
  width: 100%;
  height: 40rem;
  position: relative;
  overflow: hidden;
}
.slideshow2 ul, .slideshow2 ol {
  list-style: none;
  margin: 0;
}

.slideshow3 {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.slideshow3 ul, .slideshow3 ol {
  list-style: none;
  margin: 0;
}

.slider li, ul,
.slider2 li, ul,
.slider3 li, ul {
  width: 100%;
}

.slider li,
.slider2 li,
.slider3 li {
  overflow: hidden;
}

.slider li .img_slider {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  animation-duration: 4s;
  animation-name: slidein;
  object-fit: cover;
}

.slider2 li .img_slider {
  width: 100%;
  height: 40rem;
  overflow: hidden;
  animation-duration: 4s;
  border-radius: 3px;
}

.slider3 li .img_slider {
  width: 100%;
  height: 40rem;
  overflow: hidden;
  animation-duration: 4s;
  border-radius: 3px;
}

@keyframes slidein {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
.slider .caption {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 15px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  z-index: 1;
}

.slider .caption h1 {
  font-size: 50px;
}

.slider .caption p {
  margin-top: 10px;
  font-size: 20px;
}

.pagination {
  position: absolute;
  top: 102%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.pagination li {
  font-size: 20px;
  margin: 2px 5px;
  color: #909196;
  cursor: pointer;
}

.left, .right {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 35px;
  cursor: pointer;
  z-index: 2;
}

.left {
  left: 10px;
}

.right {
  right: 10px;
}

.total {
  margin-bottom: 10px;
  border: 0;
}

.slider_eventos {
  position: relative;
}

.slideshow, .slidershow3 {
  overflow: hidden;
}

.slider2 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slider3 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slider2 li {
  flex: 0 0 100%;
}

.slider3 li {
  flex: 0 0 100%;
}

.img_slider {
  width: 100%;
  height: 40rem;
  object-fit: cover;
}

.imagen_header2,
.imagen_header3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.imagen_header_fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.texto_overlay {
  color: white;
  font-size: 1.6rem;
  text-align: center;
  max-width: 80%; /* Ajusta el ancho mximo del texto segn tus necesidades */
  max-height: 80%; /* Ajusta la altura mxima del texto segn tus necesidades */
  overflow: hidden; /* Recorta el texto si es demasiado largo */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.texto_overlay span {
  font-weight: 600;
}
.texto_overlay a {
  display: inline-block;
  margin-top: 1rem;
  color: #fff;
  font-style: italic;
  font-size: 1.4rem;
}

.imagen_header2 {
  width: 100%;
  height: 40rem;
}
.imagen_header2 .imagen_header_fondo {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 40rem;
}
.imagen_header2 .logo_header {
  position: absolute;
  top: 20rem;
  left: 43rem;
  margin: 0;
  z-index: 2;
  width: 50rem;
}

#btnWs {
  padding: 10px;
  background-color: green;
  color: white;
}

@media screen and (max-width: 900px) {
  .seccion_registrar {
    flex-direction: column;
    height: 16rem !important;
    align-items: center !important;
    padding-right: 0rem !important;
    padding-top: 2rem;
    gap: 2rem !important;
  }
  .slider .caption p {
    display: none;
  }
  .slider .caption h1 {
    font-size: 35px;
  }
  .left, .right {
    font-size: 30px;
  }
  .slider li img {
    width: 600px;
  }
  .pagination {
    display: none;
  }
  .menu i {
    color: white;
  }
  .menu nav {
    background: #d2cdcd;
    height: 100vh;
    padding: 40px;
    position: fixed;
    right: -250px;
    top: 0;
    transition: all 0.3s;
    width: 250px;
    z-index: 19;
    display: flex !important;
    flex-direction: column;
    justify-content: center;
  }
  .menu ul {
    flex-direction: column;
    margin-top: 40px;
  }
  .menu ul li {
    margin-top: 30px;
    margin-left: 0;
  }
  .menu ul li a {
    color: #bebebe;
  }
  .header .header_cont .navegacion a {
    color: #ac1c43;
  }
  #selected {
    background: none;
    padding: 0px;
    border-radius: none;
    color: #F6615D;
  }
  .menu nav img {
    display: block;
    width: 60px;
  }
  #back_menu {
    background: rgba(0, 0, 0, 0.5);
    display: none;
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 15;
  }
  .menu #btn_menu {
    display: flex !important;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-size: 24px;
    border-radius: 50px;
    cursor: pointer;
    transition: all 300ms;
  }
  .menu #btn_menu:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  .video {
    height: 75rem;
  }
  .cuadro {
    height: 75rem;
    top: 9rem;
  }
  .video33 {
    height: 59rem;
  }
  .cuadro, .video33 {
    width: 100%;
  }
  .video33 .header-flex {
    flex-direction: column;
    margin: 0 auto;
  }
  .part2_header-flex p {
    text-align: center;
  }
  .part2_header-flex p:first-child {
    font-size: 2rem !important;
  }
  .part1_header-flex img {
    width: 10rem;
  }
  .formulario_re {
    grid-template-columns: 100%;
  }
}
/** Media Queries **/
.footer {
  background-color: #000;
}
.footer .footer_todo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
}
.footer .footer_todo .logo_footer {
  width: 45rem;
}
.footer .footer_todo .footer_titulo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer .footer_todo .footer_titulo h5 {
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
}
.footer .footer_todo .copyright {
  padding: 5rem 2rem 2rem 2rem;
  font-size: 1.2rem;
  color: #ffffff;
}

.footer_info {
  display: grid;
  justify-content: space-between;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5rem;
}
.footer_info .footer_info_part p {
  color: #ffffff;
  font-size: 1.5rem;
}
.footer_info .redes_part_footer {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0rem 3rem;
}
.footer_info .redes_part_footer .redes_div a i {
  color: #ffffff;
}

