.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.tabla_reserva {
  width: 100%;
  margin-bottom: 6rem;
  margin-top: 2rem;
  overflow-x: auto !important;
}

.tabla_reserva th {
  background: gray;
  border: none;
  color: white;
  font-weight: 400;
}

.tabla_reserva td {
  text-align: center;
}

.reserva_ver h4 {
  margin: 2rem;
}

.form_reservas a {
  color: white;
  background: #0d7aee;
  text-align: center;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-size: 1.3rem;
}
.form_reservas a:hover {
  background-color: #045c96;
  transition: 0.2s ease-in;
  cursor: pointer;
}

.boton-verde_confirmado {
  border: none;
  background: green;
  color: white;
  padding: 1rem;
  border-radius: 1rem;
  margin-top: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.boton-verde_confirmado:hover {
  background-color: rgb(55, 172, 55);
  transition: 0.2s ease-in;
  cursor: pointer;
}

.pendiente_p {
  color: #0d7aee;
  font-weight: 700;
  text-transform: uppercase;
}

.in_house {
  color: rgb(226, 108, 5);
  font-weight: 700;
  text-transform: uppercase;
}

.confirmadas_p {
  color: green;
  font-weight: 700;
  text-transform: uppercase;
}

.cancelada_p {
  color: rgb(185, 6, 6);
  font-weight: 700;
  text-transform: uppercase;
}

.body_infor_div p {
  word-wrap: break-word;
}

.acciones {
  display: flex;
  justify-content: center;
  gap: 11rem;
  margin-bottom: 3rem;
}

.contenedor.recuperar_pagina h1 {
  font-size: 2.4rem;
  color: #403e3e;
}

.recuperar_pagina p {
  font-size: 1.7rem;
}

.campo2 input {
  width: 38rem !important;
}

.campo2 label {
  text-transform: full-size-kana;
  margin-top: 1.4rem;
  color: #525353;
}

.form-recuperar .boton {
  border: none;
  background: #ac1c43;
  color: white;
  padding: 1rem;
  border-radius: 3rem;
  margin-bottom: 5rem;
}

@media screen and (max-width: 900px) {
  .campo2 input {
    width: 100% !important;
  }
  .acciones {
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    align-items: center;
  }
}